@import url(../fontawesome/fontawesome/css/all.css);
:root {
  --font-size-initial: 12px;
  --font-size-initial-lv2: 14px;
  --font-size-initial-lv3: 16px;
  --font-size-initial-lv4: 18px;
  --spacing-site: 2em;
  --color-text-main: #000;
  --color-text-header: rgb(72, 191, 22);
  --color-text-hoover: rgb(44, 76, 48); }

/*
*Witespace Reset*/
* {
  margin: 0;
  padding: 0; }

/*
*Box-sizing fix*/
html {
  -webkit-box-sizing: border-box;
  box-sizing: border-box; }

*, *::after, *::before {
  -webkit-box-sizing: inherit;
  box-sizing: inherit; }

html {
  font: var(--font-size-initial, 12px)/1.5 "Verdana", sans-serif; }

@media screen and (min-width: 500px) {
  html {
    font-size: var(--font-size-initial-lv2, 14px); } }

@media screen and (min-width: 700px) {
  html {
    font-size: var(--font-size-initial-lv3, 16px); } }

@media screen and (min-width: 1600px) {
  html {
    font-size: var(--font-size-initial-lv4, 18px); } }

body, table, th, td, form, input, textarea, option, select {
  font: inherit; }

h1, h2, h3, h4, h5, h6 {
  line-height: 1.2;
  padding-bottom: 0.5em; }

h1 {
  font-size: 2.441em; }

h2 {
  font-size: 1.953em; }

h3 {
  font-size: 1.563em; }

h4 {
  font-size: 1.25em; }

h5 {
  font-size: 1em; }

h6 {
  font-size: 1em;
  font-weight: bold; }

p, ul, ol {
  padding-bottom: 1.5em; }

ul, ol {
  padding-left: 2em; }

li {
  list-style-position: inside; }

html {
  background: url("../picture-2.jpg");
  background-size: cover;
  background-repeat: no-repeat; }

body {
  color: var(--color-text-main, #000); }

.site {
  padding: var(--spacing-site, 2em); }

@media screen and (max-width: 799px) {
  .site-navigation, header #toggle .fa-times {
    display: none; }
  #main-nav-toggle:checked + header .site-navigation {
    display: block; }
  #main-nav-toggle:checked + header .site-navigation ul {
    display: block;
    -webkit-box-flex: 1;
    -ms-flex: 1 0 100%;
    flex: 1 0 100%;
    padding-bottom: 0.5em; }
  #main-nav-toggle:checked + header #toggle .fa-times {
    display: block; }
  #main-nav-toggle:checked + header #toggle .fa-bars {
    display: none; }
  #main-nav-toggle:checked + header #toggle span {
    display: none; } }

@media screen and (min-width: 800px) {
  .site-navigation {
    margin: 1rem; } }

@media screen and (min-width: 1025px) {
  .site-navigation ul {
    padding-right: 2rem; } }

.site-navigation {
  padding-bottom: 0; }

.site-navigation ul {
  padding: 0;
  margin: 0;
  padding-left: 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: nowrap;
  flex-wrap: nowrap; }

.site-navigation li {
  text-align: center;
  display: block;
  list-style: none outside;
  margin: 0.5em;
  border: 1px solid var(--color-text-header, #4bd61f);
  border-radius: 2rem;
  background: url("../picture-2.jpg");
  background-size: cover; }

.site-navigation li a {
  color: var(--color-text-header, #4bd61f);
  text-decoration: none;
  padding: 2rem;
  font-weight: bold; }

.site-navigation li:hover,
.site-navigation li:focus {
  background: url("../picture-3.jpg");
  background-size: cover;
  border: 2px solid var(--color-text-hoover, #2c4c30); }

.site-navigation li a:hover,
.site-navigation li a:focus {
  color: var(--color-text-hoover, #2c4c30); }

@media screen and (min-width: 800px) {
  .site-header {
    text-align: center; }
  .site-header div {
    display: inline-block; }
  .site-header .menu {
    opacity: 0; } }

@media screen and (max-width: 1200px) {
  .site-header {
    text-align: center; } }

@media screen and (min-width: 1200px) {
  .site-header {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: justify;
    -ms-flex-pack: justify;
    justify-content: space-between; }
  .site-header .menu {
    margin-top: 6rem; }
  .site-navigation {
    padding-top: 4rem;
    padding-bottom: 1rem; } }

.site-header {
  border-top: 2px solid var(--color-text-header, #4bd61f);
  background: url("../picture-1.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  -webkit-box-shadow: 0.5rem 1rem 4rem 0rem rgba(72, 191, 22, 0.5);
  box-shadow: 0.5rem 1rem 4rem 0rem rgba(72, 191, 22, 0.5); }

.site-title {
  font-family: 'Times New Roman', Times, serif;
  font-size: 2.5em;
  font-weight: bold;
  text-transform: uppercase;
  padding: 1rem;
  color: var(--color-text-header, #4bd61f); }

#main-nav-toggle {
  display: none; }

.site-header .menu {
  margin-top: 3em;
  margin-bottom: 1em;
  font-size: 2em;
  display: block;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -ms-flex-direction: column;
  flex-direction: column;
  color: var(--color-text-header, #4bd61f); }

.fas {
  padding: 0.5rem; }

@media screen and (min-width: 620px) {
  .site-footer {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex; }
  .footer-nav-toggle {
    border-left: 2px solid var(--color-text-header, #48bf16); }
  .footer-title {
    width: 100%;
    border: none; } }

@media screen and (max-width: 620px) {
  .footer-title {
    margin: 0; }
  .footer-nav-toggle {
    margin: 0; }
  .footer-title {
    border-bottom: 2px solid var(--color-text-header, #48bf16); } }

.site-footer {
  background: #2C4C30;
  border-bottom: 2px solid var(--color-text-header, #48bf16);
  -webkit-box-shadow: 0.5rem -0.5rem 2.5rem 0.5rem rgba(72, 191, 22, 0.5);
  box-shadow: 0.5rem -0.5rem 2.5rem 0.5rem rgba(72, 191, 22, 0.5);
  color: var(--color-text-header, #48bf16); }

.site-footer div {
  padding: 1rem; }

.site-footer a {
  color: var(--color-text-header, #48bf16);
  text-decoration: none; }

.footer-title {
  text-align: center;
  background: url("../animals-2924020_1280.png");
  background-size: contain;
  background-position: bottom;
  background-repeat: no-repeat; }

#phone-toggle {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
  flex-wrap: wrap; }

.footer-nav-toggle {
  width: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -ms-flex-direction: column;
  flex-direction: column;
  text-align: left; }

#public-navigation {
  text-align: center;
  padding: 1rem;
  font-size: 2rem; }

#public-navigation i {
  margin-left: 1rem; }

.site-footer a:hover,
.site-footer a:focus {
  color: black;
  text-decoration: underline; }

#facebook:hover,
#facebook:focus {
  color: #4267B2; }

#twitter:hover,
#twitter:focus {
  color: #498AF4; }

#google-plus:hover,
#google-plus:focus {
  color: #EC392A; }

button {
  padding: 0.5em 1em;
  margin-bottom: 1em;
  border-radius: 1em;
  background: white;
  font-size: 1.3rem;
  border: none;
  color: var(--color-text-header, #4bd61f); }
